import React from 'react';
import TextField from '@material-ui/core/TextField';
import { withStyles } from '@material-ui/core/styles';
import { DateTimePicker, MuiPickersUtilsProvider } from '@material-ui/pickers';
import Moment from '@date-io/moment';

class MyTimeStamp extends React.Component {
    constructor(props) {
        super(props);

        this.state = {

        };
    }

    validateText(value) {
        if (isNaN(value)) { return false; }

        return true;
    }

    toDateString(value) {
        let epochTicks = 621355968000000000;
        let ticksPerMillisecond = 10000;
        let maxDateMilliseconds = 8640000000000000;
        if (!value) {
            return "";
        }
        let ticksSinceEpoch = value - epochTicks;
        let millisecondsSinceEpoch = ticksSinceEpoch / ticksPerMillisecond;
        if (millisecondsSinceEpoch > MediaStreamAudioDestinationNode) {
            console.error("Invalid number");
        }

        var date = new Date(millisecondsSinceEpoch);
        return date.toISOString();
    }

    ticksChanged(event) {
        let value = event.target.value;

        if (!this.validateText(value)) {
            this.setState({ errorText: "Input a number" });
            return;
        } else {
            this.setState({ errorText: "" });
        }

        let dateString = this.toDateString(value);
        this.setState({ datetime: dateString });
    }

    unixChanged(event) {

    }

    unixChanged2(event) {

    }

    timeChanged(event) {
        console.log(event.target.value);
    }

    render() {
        const { classes } = this.props;

        return (
            <div>
                <TextField
                    id="unix-s-textarea"
                    className={classes.textField}
                    label="Unix Time (s)"
                    placeholder="Unix Time (s)"
                    color="primary"
                    variant="standard"
                    onChange={this.unixChanged.bind(this)}
                >
                </TextField>

                <TextField
                    id="unix-ms-textarea"
                    className={classes.textField}
                    label="Unix Time (ms)"
                    placeholder="Unix Time (ms)"
                    color="primary"
                    variant="standard"
                    onChange={this.unixChanged2.bind(this)}
                >
                </TextField>

                <TextField
                    id='ticks-textarea'
                    className={classes.textField}
                    label="Time Ticks"
                    placeholder="Ticks"
                    color="primary"
                    variant="standard"
                    onChange={this.ticksChanged.bind(this)}
                >
                </TextField>

                <MuiPickersUtilsProvider utils={Moment}>
                    <DateTimePicker
                        variant="standard"
                        label="UTC Time"
                        className={classes.textField}
                        placeholder="UTC Time"
                        onChange={this.timeChanged.bind(this)}
                    >
                    </DateTimePicker>
                </MuiPickersUtilsProvider>

            </div>
        );
    }
}



const styles = theme => ({
    textField: {
        margin: theme.spacing(2),
        width: 300,
        display: "block",
    },

    typography: {
        display: "inline-block",
    },
});

export default withStyles(styles, { withTheme: true })(MyTimeStamp);

